
.button-hamburgerIcon {
  width: 30px;
  border: none;
  cursor: pointer;
  outline: none;
  display: block;
  margin-top: 15px;
  margin-left: auto;
  margin-right: auto 
  
}

.button-hamburgerIcon .img-hamburger{
  position: fixed;
    height: 32px;
    vertical-align: middle;
    transition-duration: 0.001s;

}

.button-hamburgerIcon:active{
  -webkit-transform: scale(0.85);
      transform: scale(0.85);
}

.div-menu{
  margin-top: 49px;
  background-color: #14343f;
  width: 100vw;
  margin-left: calc(-100vw + 100%);
}
.div-menu ul{
  list-style: none;
}

.div-menu ul li{
  color: #fff;
  
}